<template>
  <v-layout>
    {{creditoTramitado}}
    <label for="input" class="w-100">
      <div class="input-file-button d-block">{{ fieldname }}</div>
    </label>
    <input id="input" type="file" class="input-file-hidden" @change="sendFile">
  </v-layout>
</template>

<script>
import Api from "@/services/api.js";
import { mapState } from "vuex";

export default {
  name: "inputFile",
  props: ["fieldname"],

  data() {
    return {
      tipe: this.fieldname
    };
  },
  computed: {
    ...mapState(["creditoTramitado"])
  },
  methods: {
    sendFile() {
      const file = document.getElementById("input").files[0];
      const data = {
        file,
        fieldname: this.fieldname,
        cid: this.creditoTramitado
      };

      Api.file("files/uploads.php", data, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(response => {
          console.log(response);
        })
        .catch(e => {
          console.log(e);
        });
    }
  }
};
</script>

<style>
.input-file-button {
  background: #2096f3;
  border-radius: 5px;
  padding: 10px;
  color: #fff;
  transition: 0.5s;
  text-align: center;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
  width: 100%;
  margin-bottom: 0.5rem;
}

.w-100 {
  width: 100%;
}

.input-file-button:hover {
  filter: brightness(120%);
  transition: 0.5s;
}

.input-file-hidden {
  display: none;
}
</style>