<script>
import SignUp from "./SignUp.vue";
export default {
  props: ["banco", "bancoId", "creditoPreautorizado"],

  components: {
    SignUp
  },
  data() {
    return {
      dialog: false,
      share: false
    };
  },
  computed: {},
  methods: {
    shareText(platform) {
      let platforms = {
        whatsapp: `
        Crédito Hipotecario en *${this.banco.nombre.toUpperCase()}* %0A
        Mensualidad: *${this.$options.filters.currency(
          this.banco.valores.mensualidad.toFixed(2)
        )}* %0A
        Ingresos: *${this.$options.filters.currency(
          this.banco.valores.ingresos.toFixed(2)
        )}* %0A
        Tasa fija anual: *${(this.banco.valores.tasa * 100).toFixed(2)} %* %0A
        Crédito Solicitado: *${this.$options.filters.currency(
          this.banco.valores.montoDeCredito.toFixed(2)
        )}* %0A
        Crédito Pre autorizado: *${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}* %0A %0A
        Tramitalo en https://creditohipotecarioexpress.com
       `,
        email: `
        Crédito Hipotecario en ${this.banco.nombre.toUpperCase()} %0A
        Mensualidad: ${this.$options.filters.currency(
          this.banco.valores.mensualidad.toFixed(2)
        )} %0A
        Ingresos: ${this.$options.filters.currency(
          this.banco.valores.ingresos.toFixed(2)
        )} %0A
        Tasa fija anual: ${(this.banco.valores.tasa * 100).toFixed(2)} % %0A
        Crédito Solicitado: ${this.$options.filters.currency(
          this.banco.valores.montoDeCredito.toFixed(2)
        )} %0A
        Crédito Pre autorizado: ${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )} %0A %0A
        Tramitalo en https://creditohipotecarioexpress.com`
      };
      return platforms[platform];
    },
    shareTest() {
      navigator.share({
        title: "Tramita este credito en creditohipotecarioexpress.com",
        text: this.shareText("email"),
        url: "https://creditohipotecarioexpress.com"
      });
    }
  },
  mounted() {}
};
</script>
